import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class UsersManagementService {

  constructor(private http: HttpClient) { }



  getUsers(): Observable<any>
  {
    const UsersUrl =`${environment.baseUrlAdmin}/api/v1/users/all`; 
    return this.http.get<any[]>(UsersUrl);
  }

  getUsersById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlAdmin}/api/v1/users/find/by/id`, {params}
    );
  }

  getUsersByStatus(params): Observable<any> {
    const UsersUrl = `${environment.baseUrlAdmin}/api/v1/users/fetchbyStatus`;
    return this.http.get<any[]>(UsersUrl, { params });
  }

//adding cost centres
addUser(data: any): Observable<any>
  {
    const UsersUrl =`${environment.baseUrlAdmin}/api/v1/users/create`; 
    return this.http.post<any[]>(UsersUrl, data);
  }

//updating cost centres
updateUser(data: any): Observable<any>
  {
    const UsersUrl =`${environment.baseUrlAdmin}/api/v1/users/update`; 
    return this.http.put<any[]>(UsersUrl, data);
  }

  accountLockUnlock(params: any): Observable<any>
  {
    const UsersUrl =`${environment.baseUrlAdmin}/api/v1/users/update/access`; 
    return this.http.put<any[]>(UsersUrl,{},{params});
  }


deleteUserTemporarily(params: any): Observable<any>
  {
    const UsersUrl =`${environment.baseUrlAdmin}/api/v1/users/delete`; 
    return this.http.delete<any[]>(UsersUrl, {params});
  }


uploadBatchUsers(data: any): Observable<any> 
  {
    const UsersUrl = `${environment.baseUrlAdmin}/api/v1/users/create/bulk`;
    return this.http.post<any>(UsersUrl, data);
  }



validateUsers(dataToValidate: any): Observable<any> {
const imprestUrl = `${environment.baseUrlAdmin}/api/v1/users/update/state`;
return this.http.put<any>(imprestUrl, dataToValidate);
}



}
